<template>
	<div id="AskForLeave">
		<ul>
			<li>
				<span><b><img :src="userData.avatar" alt=""></b></span>
				<p>{{userData.name}}</p>
			</li>
			<!--  -->
			<li v-if="messageType == 2">
				<span></span>
				<p>{{userData.belongDept}}</p>
			</li>
			<li v-if="messageType == 2">
				<span></span>
				<p>{{userData.num}}</p>
			</li>
			<li v-if="messageType == 2">
				<span></span>
				<p>{{userData.types}}</p>
			</li>
			<li v-if="messageType == 2">
				<span></span>
				<p>{{userData.reason}}</p>
			</li>
			<li v-if="messageType == 2">
				<span></span>
				<p>{{userData.isOutSchool}}</p>
			</li>
      

			<!--  -->
			<li v-if="messageType == 3">
				<span></span>
				<p>{{userData.belongDept}}</p>
			</li>
			<li v-if="messageType == 3">
				<span></span>
				<p>{{userData.num}}</p>
			</li>
			<li v-if="messageType == 3">
				<span></span>
				<p>{{userData.types}}</p>
			</li>
      


			<li>
				<span></span>
				<p>{{userData.startTime}}</p>
			</li>
			<li>
				<span></span>
				<p>{{userData.endTime}}</p>
			</li>
			<li>
				<span></span>
				<p>{{userData.duration}}</p>
			</li>
			<li>
				<span></span>
				<p>{{userData.reason}}</p>
			</li>


      <li v-if="messageType == 2 && userData.attachment != ''">
        <span></span>
        <div>
          <p>
            <b><img :src="userData.attachment" alt=""></b>
          </p>
        </div>
      </li>

      <li v-if="messageType == 3 && attachment != ''">
        <span></span>
        <div>
          <p>
            <b v-for="(item , index) in attachment"><img :src="item.attachermentUrl" alt=""></b>
          </p>
        </div>
      </li>

		</ul>
		<div class="status"><img :src="finalResultUrl" alt=""></div>
		<div class="classSwitching" v-if="messageType == 3">
			<div class="classSwitchingList" v-for="(item , index) in coursesList">
				<h3>{{index+1}}</h3>
				<div class="classSwitchingListDetails">
					<div>
						<p class="name">{{item.teacherLeaveName}}</p>
						<p class="hour">{{item.coursesDate}}{{item.coursesSection}}</p>
						<p class="subject">{{item.coursesName}}</p>
					</div>
					<div>
						<p class="name">{{item.substituteTeacherName}}</p>
						<p class="hour">{{item.substituteCoursesDate}}{{item.substituteCoursesSection}}</p>
						<p class="subject">{{item.substituteCoursesName}}</p>
					</div>
					<span><img src="../assets/images/icon_transform.jpg" alt=""></span>

				</div>
			</div>
		</div>
		<div class="agreeOrNot">
			<div class="agreeOrNotList" v-for="(item, index) in approvalNode">
				<span v-if="userRole == 'teacher'"><img src="../assets/images/icon_pitchOn2.png" alt=""></span>
				<span v-else><img src="../assets/images/icon_pitchOn1.png" alt=""></span>
				<div class="agreeOrNotListLeft">
					<div class="tle">
						<a v-if="item.nodeName == ''">{{item.nodeName}}</a>
						<a v-else>
              <em v-if="item.deptName != '' || item.name != ''">{{item.deptName}}</em>
              <em v-else>{{item.nodeName}}</em>
            </a>

						<b v-if="item.nodeName == ''">
              <em v-if="item.deptName != '' || item.name != ''">({{item.deptName}}-{{item.name}})</em>
            </b>
						<b v-else>
              <em v-if="item.name != ''">({{item.name}})</em>
            </b>
           <!--  <a>{{item.nodeName}}</a>
            <b>
              <em v-if="item.deptName != '' || item.name != ''">({{item.deptName}}-{{item.name}})</em>
            </b> -->
            

						<i :class="userRole == 'teacher' ? 'teacher' : '' ">{{item.result}}</i>
					</div>
					<div class="details" v-if="item.reason != ''">
						<p>{{item.reason}}</p>
						<time>{{item.approvalTime}}</time>
					</div>
				</div>
				<div class="blank" v-if="item.reason != ''"></div>
			</div>
		</div>
	</div>
</template>

<script>
//import xxx from 'xxx.vue'  //
//import 'xxxx'  //JS

export default {
  name: "AskForLeave",
  data() {
    return {
      url: this.$store.state.userDataUrl3,
      finalResultUrl: "",
      userId: sessionStorage.userId,
      userRole: sessionStorage.userRole,
      messageType: 0,
      coursesList: [],
      userData: [],
      attachment: [],
      approvalNode: []
    };
  },
  components: {
    // 
  },
  mounted() {
    const that = this;
    that.messageType = that.$route.query.messageType;
    if (that.messageType == "3") {
      that
        .$http({
          method: "GET",
          url:
            that.url +
            "api/leave/teacher/v0.1/detailIds/" +
            that.$route.query.leaveId,
          headers: {
            accessToken: that.userId
          }
        })
        .then(function(response) {
          // console.log(response);

          that.userData = response.data.leaveInfo;
          that.attachment = response.data.attachments;
          that.userData.types = that.TeacherLeaveTypeEnum(that.userData.type);
          that.finalResultUrl = that.finalResult(that.userData.finalResult);

          that.coursesList = response.data.leaveInfo.courses;
          that.coursesList.map((item, index) => {
            item.coursesSection = that.courseSectionEnum(item.coursesSection);
            item.substituteCoursesSection = that.courseSectionEnum(
              item.substituteCoursesSection
            );
          });

          that.approvalNode = response.data.approvalNodes;
          that.approvalNode.map((item, index) => {
            item.result = that.TeacherLeaveApprovalResultEnum(item.result);
          });

          // console.log(that.approvalNode);
        })
        .catch(function(error) {
          console.log(error);
        });
    } else if (that.messageType == "2") {
      that
        .$http({
          method: "GET",
          url:
            that.url + "api/leave/v0.2/detailIds/" + that.$route.query.leaveId,
          headers: {
            accessToken: that.userId
          }
        })
        .then(function(response) {
          that.userData = response.data.leaveInfo;
          that.userData.types = that.TeacherLeaveTypeEnum(that.userData.type);
          that.userData.isOutSchool = that.IsOutSchEnum(
            that.userData.isOutSchool
          );
          that.finalResultUrl = that.finalResult(that.userData.finalResult);

          that.approvalNode = response.data.approvalNodes;
          that.approvalNode.map((item, index) => {
            item.result = that.TeacherLeaveApprovalResultEnum(item.result);
          });
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  created() {
    // var qs=require('qs');
    document.title = "";
    document.body.style.background = "#F8F8F8";
    const that = this;
    that.common.setingTtitle('')
  },
  methods: {
    courseSectionEnum: num => {
      switch (num) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
        case 6:
          return "";
          break;
        case 7:
          return "";
          break;
        case 8:
          return "";
          break;
        case 9:
          return "";
          break;
        case 10:
          return "";
          break;
        case 11:
          return "";
          break;
        case 12:
          return "";
          break;
        default:
          console.log("");
      }
    },

    TeacherLeaveTypeEnum: num => {
      switch (num) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
        case 6:
          return "";
          break;
        case 7:
          return "";
          break;
        case 8:
          return "";
          break;
        case 9:
          return "";
          break;
        default:
          console.log("");
      }
    },

    TeacherLeaveApprovalResultEnum: num => {
      switch (num) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        default:
          console.log("");
      }
    },

    finalResult: num => {
      switch (num) {
        case 1: //
          return require("../assets/images/seal_proceed.png");
          break;
        case 2: //
          return require("../assets/images/seal_accomplish.png");
          break;
        case 3: //
          return require("../assets/images/seal_reject.png");
          break;
        case 4: //
          return require("../assets/images/seal_revocation.png");
          break;
        case 5: //
          return require("../assets/images/seal_pastDue.png");
          break;
        case 6: //
          return require("../assets/images/seal_get_out.png");
          break;
        case 7: //
          return require("../assets/images/seal_accomplish.png");
          break;
        case 8: //
          return require("../assets/images/seal_overtime.png");
          break;
        case 9: //
          return require("../assets/images/seal_return.png");
          break;
        case 10: //
          return require("../assets/images/seal_return.png");
          break;
        default:
          console.log("");
      }
    },

    IsOutSchEnum: num => {
      switch (num) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
          break;
        default:
          console.log("");
      }
    }
  }
};
</script>

<style lang="scss" scoped="">
/*@import ''*/
#AskForLeave {
  position: relative;
  ul {
    background: #fff;
    li {
      border-bottom: 1px solid #f8f8f8;
      display: flex;
      align-items: center;
      padding: 0.2rem 0.3rem;
      font-size: 0.32rem;
      color: rgba(51, 51, 51, 0.87);
      span {
        width: 1.84rem;
        overflow: hidden;
        b {
          display: block;
          width: 1.04rem;
          height: 1.04rem;
          overflow: hidden;
          border-radius: 50%;
        }
      }
      div {
        flex: 1;
        p {
          display: flex;
          overflow: hidden;
          width: 5rem;
          b {
            margin-right: 0.1rem;
            width: 1.6rem;
            height: 1.6rem;
            overflow: hidden;
            img {
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }
  }
  .status {
    position: absolute;
    right: 0.32rem;
    top: 0.22rem;
    width: 2rem;
    height: 2rem;
    img {
      width: 100%;
    }
  }
  .classSwitching {
    .classSwitchingList {
      margin-top: 0.2rem;
      background: #fff;
      h3 {
        padding: 0 0.32rem;
        height: 1rem;
        line-height: 1rem;
        font-size: 0.3rem;
        color: #8798a4;
        font-weight: normal;
        border-bottom: 1px dashed #e0e0e0;
      }
      .classSwitchingListDetails {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 0.28rem;
        span {
          position: absolute;
          width: 0.4rem;
          height: 0.48rem;
          top: 0;
        }
        div {
          flex: 1;
          height: 100%;
          text-align: center;
          font-size: 0.3rem;
          color: #333333;
          line-height: 0.68rem;
          padding-bottom: 0.14rem;
          &:nth-of-type(1) {
            border-right: 1px solid #e0e0e0;
          }
        }
      }
    }
  }
  .agreeOrNot {
    overflow: hidden;
    .agreeOrNotList {
      border-bottom: 1px solid #f8f8f8;
      background: #fff;
      position: relative;
      margin-top: 0.2rem;
      span {
        position: absolute;
        width: 0.28rem;
        height: 0.28rem;
        top: 0.36rem;
        left: 0.55rem;
      }
      .agreeOrNotListLeft {
        padding-right: 0.36rem;
        margin-left: 0.68rem;
        border-left: 1px solid #e0e0e0;
        overflow: hidden;
        overflow: hidden;
        .tle {
          display: flex;
          align-items: center;
          height: 1rem;
          padding-left: 0.34rem;
          a {
            font-size: 0.32rem;
            color: rgba(51, 51, 51, 0.87);
            padding-right: 0.2rem;
            em {
              font-style: normal;
            }
          }
          b {
            font-size: 0.28rem;
            color: rgba(102, 102, 102, 0.87);
            flex: 1;
            font-weight: normal;
            em {
              font-style: normal;
            }
          }
          i {
            font-size: 0.32rem;
            color: #62c095;
            font-style: normal;
            &.teacher {
              color: #03a9f4;
            }
          }
        }
        .details {
          margin-left: 0.16rem;
          background: #f8f8f8;
          padding: 0.18rem 0.24rem;
          p {
            font-size: 0.3rem;
            color: rgba(51, 51, 51, 0.87);
          }
          time {
            font-size: 0.26rem;
            color: rgba(153, 153, 153, 0.87);
          }
        }
      }
      .blank {
        height: 0.28rem;
        width: 100%;
      }
    }
  }
}
</style>
